/* --- Componentes auxiliares --- */

type TabButtonProps = {
  isActive: boolean
  onClick: () => void
  children: React.ReactNode
}

function TabButton({ isActive, onClick, children }: TabButtonProps) {
  return (
    <button
      type="button"
      onClick={onClick}
      className={`px-4 py-1.5 text-sm rounded-full flex items-center gap-1 transition-all ${isActive ? "bg-primary text-primary-foreground shadow-sm" : "text-muted-foreground hover:text-foreground"
        }`}
    >
      {children}
    </button>
  )
}

type MapViewWrapperProps = {
  children: React.ReactNode
}

function MapViewWrapper({ children }: MapViewWrapperProps) {
  return (
    <motion.div
      initial={{ opacity: 0, y: 20, scale: 0.98 }}
      animate={{ opacity: 1, y: 0, scale: 1 }}
      exit={{ opacity: 0, y: -15, scale: 0.98 }}
      transition={{ duration: 0.35, ease: [0.22, 0.61, 0.36, 1] }}
      className="flex flex-col"
    >
      {children}
    </motion.div>
  )
}

type StepCardProps = {
  title: string
  subtitle: string
  description: string
  align?: "left" | "right"
}

function StepCard({ title, subtitle, description, align = "left" }: StepCardProps) {
  return (
    <div
      className={`flex-1 max-w-xs mx-auto text-center ${align === "left" ? "md:text-left" : "md:text-right"
        }`}
    >
      <p className="text-xs uppercase tracking-[0.18em] text-muted-foreground mb-1">{title}</p>
      <p className="font-medium text-foreground mb-1">{subtitle}</p>
      <p className="text-sm text-foreground/70">{description}</p>
    </div>
  )
}
